import { mount } from 'svelte';
import App from './App.svelte';
import appCss from './assets/app.css?inline';
import type { WidgetSettings } from './lib/types';
import ChatButton from './lib/components/ChatButton.svelte';
import { widgetApi } from './lib/utils/api';

// Export the widget class as a named export
export class FluentBotChatWidget {
    private container: HTMLElement | null = null;
    private buttonContainer: HTMLElement | null = null;
    private isOpen: boolean = false;
    private clickHandler: (event: MouseEvent) => void;
    private settings: WidgetSettings | null = null;
    private fluid: boolean = false;

    constructor(botId: string, fluid: boolean = false) {
        this.clickHandler = this.handleClickOutside.bind(this);
        this.fluid = fluid;
        this.initialize(botId);
    }

    private async fetchSettings(botId: string): Promise<WidgetSettings | null> {
        try {
            const response = await widgetApi.get(`/api/bots/${botId}/widget-settings`);
            return response.data.data;
        } catch (error) {
            console.error('Error fetching widget settings:', error);
            return null;
        }
    }

    private handleClickOutside(event: MouseEvent) {
        const path = event.composedPath();

        // Check if click is outside the widget
        const isOutside = !path.some(element =>
            element === this.container ||
            element === this.buttonContainer ||
            (element instanceof HTMLElement && (
                element.closest('#fluent-bot-chat-widget') === this.container
                || element.closest('#chat-button') === this.buttonContainer
            ))
        );

        if (isOutside && this.isOpen) {
            this.toggleWidget();
        }
    }

    private toggleWidget() {
        this.isOpen = !this.isOpen;
        if (this.container) {
            if (this.isOpen) {
                this.container.style.display = 'flex';
                // Trigger animation after display is set
                setTimeout(() => {
                    this.container!.style.opacity = '1';
                    this.container!.style.transform = 'translateY(0) scale(1)';
                }, 0);
            } else {
                this.container.style.opacity = '0';
                this.container.style.transform = 'translateY(20px) scale(0.95)';
                // Hide after animation
                setTimeout(() => {
                    this.container!.style.display = 'none';
                }, 300);
            }
        }
    }

    private createStyles() {
        const style = document.createElement('style');
        style.textContent = `
            @import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');
            
            :host {
                position: fixed;
                bottom: 20px;
                z-index: 9999;
                display: flex;
                flex-direction: column;
            }

            :host(.align-right) {
                right: 20px;
                align-items: flex-end;
            }

            :host(.align-left) {
                left: 20px;
                align-items: flex-start;
            }

            :host(.fluid) {
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                width: 100%;
                height: 100%;
                align-items: center;
                justify-content: center;
            }

            #fluent-bot-chat-widget {
                width: 400px;
                height: 600px;
                max-height: calc(100vh - 100px);
                overflow: visible;
                display: none;
                flex-direction: column;
                position: relative;
                background: white;
                border-radius: 1rem;
                box-shadow: 
                    rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
                    rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
                margin-bottom: 1rem;
                opacity: 0;
                transform: translateY(20px) scale(0.95);
                transform-origin: bottom;
                transition: opacity 0.3s ease, transform 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
                overflow: hidden;
            }

            :host(.fluid) #fluent-bot-chat-widget {
                width: 100%;
                height: 100%;
                max-height: 100%;
                margin: 0;
                border-radius: 0;
                transform: translateY(0) scale(0.95);
            }

            :host(.align-right) #fluent-bot-chat-widget {
                transform-origin: bottom right;
            }

            :host(.align-left) #fluent-bot-chat-widget {
                transform-origin: bottom left;
            }

            :host(.fluid) #fluent-bot-chat-widget {
                transform-origin: center;
            }

            /* Mobile and small device styles */
            @media screen and (max-width: 640px) {
                :host {
                    bottom: 0;
                    left: 0 !important;
                    right: 0 !important;
                    width: 100%;
                    height: 100%;
                    align-items: stretch !important;
                }

                #fluent-bot-chat-widget {
                    width: 100%;
                    height: 100%;
                    max-height: 100%;
                    margin: 0;
                    border-radius: 0;
                    transform: translateY(100%);
                }

                :host(.align-right) #fluent-bot-chat-widget,
                :host(.align-left) #fluent-bot-chat-widget {
                    transform-origin: bottom;
                }

                #fluent-bot-chat-widget[style*="display: flex"] {
                    transform: translateY(0) !important;
                }

                /* Hide chat button when widget is open on mobile */
                #fluent-bot-chat-widget[style*="display: flex"] ~ #chat-button-container {
                    display: none;
                }

                /* Reset chat button container position when widget is closed */
                #chat-button-container {
                    position: fixed;
                    bottom: 20px;
                    z-index: 9999;
                }

                :host(.align-right) #chat-button-container {
                    right: 20px;
                }

                :host(.align-left) #chat-button-container {
                    left: 20px;
                }
            }

            ${appCss}
        `;
        return style;
    }

    private async initialize(botId: string) {
        // Fetch settings first
        this.settings = await this.fetchSettings(botId);
        
        // Don't initialize if no settings
        if (!this.settings) {
            console.warn('No widget settings found, widget will not be initialized');
            return;
        }

        // Create container
        const container = document.createElement('div');
        container.id = 'fluent-bot-chat-widget-container';
        container.classList.add(`align-${this.settings.button_alignment}`);
        
        // Add fluid class if fluid is true
        if (this.fluid) {
            container.classList.add('fluid');
            // Set isOpen to true for fluid mode
            this.isOpen = true;
        }

        // Create shadow root
        const shadowRoot = container.attachShadow({ mode: 'open' });

        // Create chat container
        const chatContainer = document.createElement('div');
        chatContainer.id = 'fluent-bot-chat-widget';

        // Create button container
        const buttonContainer = document.createElement('div');
        buttonContainer.id = 'chat-button-container';

        // Add styles to shadow root
        shadowRoot.appendChild(this.createStyles());

        // Add elements to shadow root
        shadowRoot.appendChild(chatContainer);
        
        // Only add button container if not in fluid mode
        if (!this.fluid) {
            shadowRoot.appendChild(buttonContainer);
        }

        // Add to document
        document.body.appendChild(container);

        // Store references
        this.container = chatContainer;
        this.buttonContainer = this.fluid ? null : buttonContainer;

        // Add event listeners
        document.addEventListener('click', this.clickHandler, true);
        document.addEventListener('close-widget', () => {
            if (this.isOpen) {
                this.toggleWidget();
            }
        });

        // Mount the Svelte apps with settings
        mount(App, {
            target: chatContainer,
            props: {
                settings: this.settings
            }
        });

        // Only mount chat button if not in fluid mode
        if (!this.fluid) {
            mount(ChatButton, {
                target: buttonContainer,
                props: {
                    isOpen: this.isOpen,
                    onToggle: () => this.toggleWidget(),
                    buttonIcon: this.settings.button_icon,
                    showButtonText: this.settings.show_button_text,
                    buttonText: this.settings.button_text,
                    colorHex: this.settings.color_hex,
                    lighterColorHex: this.settings.lighter_color_hex
                }
            });
        }
        
        // Show container by default if in fluid mode
        if (this.fluid && this.container) {
            this.container.style.display = 'flex';
            this.container.style.opacity = '1';
            this.container.style.transform = 'translateY(0) scale(1)';
        }
    }

    destroy() {
        if (this.container) {
            const container = this.container.closest(
                '#fluent-bot-chat-widget-container'
            );
            if (container) {
                container.remove();
            }
        }
        document.removeEventListener('click', this.clickHandler, true);
    }
}

const widgetExists = () => document.querySelector(
    '#fluent-bot-chat-widget-container'
);

// Export the inject function
export const injectWidget = (botId: string, fluid: boolean = false) => {
    if (!widgetExists()) {
        new FluentBotChatWidget(botId, fluid);
    }
};
