<script lang="ts">
  import {
    MessageSquare,
    MessageSquareMore,
    Monitor,
    LifeBuoy,
    HelpCircle,
    Menu,
    Info,
    FileText,
  } from "lucide-svelte";
  import { formatMarkdown } from "../utils/markdown";

  const {
    content,
    type,
    timestamp,
    botIcon,
    colorHex,
    lighterColorHex,
    isStreaming = false,
    hasStarted = false,
  } = $props();

  const iconMap = {
    MessageSquare: MessageSquare,
    MessageSquareMore: MessageSquareMore,
    Monitor: Monitor,
    LifeBuoy: LifeBuoy,
    HelpCircle: HelpCircle,
    Menu: Menu,
    Info: Info,
    FileText: FileText,
  };

  // Check if the botIcon is a URL (starts with http:// or https://)
  const isIconUrl = botIcon && (botIcon.startsWith('http://') || botIcon.startsWith('https://'));
  const BotIcon = $derived(
    !isIconUrl && botIcon && iconMap[botIcon as keyof typeof iconMap]
      ? iconMap[botIcon as keyof typeof iconMap]
      : MessageSquare
  );

  const formatTime = (date: Date) => {
    return date.toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" });
  };

  const formattedContent = $derived(type === 'bot' ? formatMarkdown(content) : content);
</script>

<div
  class="message-container {type === 'user' ? 'user-message' : 'bot-message'}"
>
  <div
    class="message-content {type === 'user' ? 'user-content' : 'bot-content'}"
  >
    {#if botIcon && (type === "user" || type === "bot")}
      <div
        class="avatar {isIconUrl && type === 'bot' ? 'icon-url' : ''}"
        style="background-color: {type === 'user' || isIconUrl ? 'transparent' : colorHex}"
      >
        {#if type === "user"}
          <span>U</span>
        {:else if isIconUrl}
          <img src={botIcon} alt="Bot Icon" class="avatar-image" />
        {:else if BotIcon}
          <BotIcon size={20} strokeWidth={2} color="white" />
        {/if}
      </div>
    {/if}
    <div class="bubble-container">
      <div
        class="message-bubble {type === 'user' ? 'user-bubble' : 'bot-bubble'} {isStreaming ? 'streaming' : ''}"
        style="--message-color: {type === 'user'
          ? lighterColorHex
          : 'white'}; --text-color: {type === 'user' ? 'white' : '#1f2937'}"
      >
        {#if type === "user"}
          <div class="message-text">{content}</div>
        {:else if isStreaming && !hasStarted}
          <div class="thinking">
            <span>Thinking</span>
            <div class="dots-container">
              <span class="dot"></span>
              <span class="dot"></span>
              <span class="dot"></span>
            </div>
          </div>
        {:else}
          <div class="markdown-content {isStreaming ? 'typing-animation' : ''}">
            {@html formattedContent}
          </div>
        {/if}
      </div>
      <div
        class="timestamp {type === 'user' ? 'user-timestamp' : 'bot-timestamp'}"
      >
        {formatTime(timestamp)}
      </div>
    </div>
  </div>
</div>

