  }

  .dot:nth-child(2) {
    animation-delay: -0.16s;
  }

  .typing-animation {
    animation: typing 0.5s ease-in-out;
  }

  @keyframes typing {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
</style>
