import { writable, derived, get } from 'svelte/store';
import type { Message } from '../types';
import { chatApi } from '../utils/api';

export interface ChatStore {
    messages: Message[];
    isLoading: boolean;
    currentChatId: string | null;
    error: string | null;
    hasMore: boolean;
    currentPage: number;
}

const initialState: ChatStore = {
    messages: [],
    isLoading: false,
    currentChatId: null,
    error: null,
    hasMore: true,
    currentPage: 1
};

export const chatStore = writable<ChatStore>(initialState);

// Derived stores for computed values
export const formattedMessages = derived(chatStore, ($store) =>
    $store.messages.map(msg => ({
        ...msg,
        message: msg.message
    }))
);

export const sendMessage = async (content: string, settings: { bot_id: string; custom_prompt: string }) => {
    const userId = localStorage.getItem('userId');
    const botId = settings.bot_id;
    const customPrompt = settings.custom_prompt;

    if (!userId || !botId) {
        throw new Error('User ID and Bot ID are required');
    }

    // Snapshot store BEFORE we add the streaming placeholder to detect "first send"
    const prevState = get(chatStore);

    let previousText = '';
    let accumulatedText = '';

    // Add user message
    chatStore.update(state => ({
        ...state,
        messages: [
            ...state.messages,
            {
                id: Date.now().toString(),
                chatId: state.currentChatId || '',
                role: 'human',
                message: content,
                created_at: new Date().toISOString(),
            },
            {
                id: 'streaming',
                chatId: state.currentChatId || '',
                role: 'ai',
                message: '',
                created_at: new Date().toISOString(),
            },
        ],
        isLoading: true,
    }));

    try {
        // Only include conversation_id if we already have messages in the current session
        // This avoids sending stale IDs (e.g., from localStorage) on the first message
        const includeConversation = !!prevState.currentChatId && prevState.messages.length > 0;
        const body: any = {
            bot_id: botId,
            prompt: content,
            stream: true,
        };
        if (includeConversation) {
            body.conversation_id = prevState.currentChatId;
        }

        await chatApi.post('/chat/completion', body, {
            responseType: 'text',
            headers: {
                Accept: 'text/event-stream',
            },
            onDownloadProgress: (progressEvent) => {
                const text = (progressEvent as any).event?.target?.responseText || '';
                if (!text || text === previousText) return;

                const newContent = text.slice(previousText.length);
                previousText = text;

                const lines = newContent.split('\n');
                let currentEvent = '';

                for (const line of lines) {
                    if (!line) continue;
                    if (line.startsWith('event:')) {
                        currentEvent = line.replace('event:', '').trim();
                        continue;
                    }
                    if (line.startsWith('data:')) {
                        const payload = line.replace('data:', '');

                        // Handle events from backend: 'message', 'conversation_id', 'token_usage'
                        if (currentEvent === 'message') {
                            if (payload.trim() === '<|>') {
                                // finalize streaming message
                                chatStore.update(s => {
                                    const finalMessage: Message = {
                                        id: Date.now().toString(),
                                        chatId: s.currentChatId || '',
                                        role: 'ai',
                                        message: accumulatedText,
                                        created_at: new Date().toISOString()
                                    };
                                    return {
                                        ...s,
                                        messages: s.messages.map(m => m.id === 'streaming' ? finalMessage : m),
                                        isLoading: false
                                    };
                                });
                            } else if (payload) {
                                accumulatedText += payload;
                                chatStore.update(s => ({
                                    ...s,
                                    messages: s.messages.map(m => m.id === 'streaming' ? { ...m, message: accumulatedText } : m)
                                }));
                            }
                        } else if (currentEvent === 'conversation_id') {
                            const chatId = payload.trim(); // plain string
                            localStorage.setItem('currentChatId', chatId);
                            chatStore.update(s => ({
                                ...s,
                                currentChatId: chatId,
                                messages: s.messages.map(m => !m.chatId ? { ...m, chatId } : m)
                            }));
                        } else if (currentEvent === 'token_usage') {
                            // Optional: parse and store token usage if needed
                            // const usage = payload.startsWith('{') ? JSON.parse(payload) : payload;
                        }
                    }
                }
            },
        });
    } catch (error) {
        console.error('Error sending message:', error);
        chatStore.update(s => ({
            ...s,
            isLoading: false
        }));
        throw error;
    }
};

export const clearMessages = () => {
    localStorage.removeItem('currentChatId');
    chatStore.update(state => ({
        ...state,
        messages: [],
        currentChatId: null,
        hasMore: true,
        currentPage: 1
    }));
};

export const resetFilters = () => {
    chatStore.update(state => ({
        ...state,
        messages: [],
        currentChatId: null,
    }));
};

export const loadChatHistory = async (chatId: string, page: number = 1, limit: number = 10) => {
    try {
        const response = await chatApi.get(`/chat/${chatId}/messages`, {
            params: {
                page,
                page_size: limit,
            }
        });

        const rawMessages = (response.data?.messages || []) as any[];
        // Normalize and ensure messages are chronological (oldest -> newest)
        const mapped = (rawMessages.map((m, idx) => ({
            id: (m.id ?? `${Date.now()}-${idx}`).toString(),
            chatId: chatId,
            role: (m.role ?? m.type) === 'human' ? 'human' : 'ai',
            message: m.message ?? m.content ?? '',
            created_at: m.created_at ?? new Date().toISOString(),
        })) as Message[]).sort((a, b) => {
            const ta = new Date(a.created_at).getTime();
            const tb = new Date(b.created_at).getTime();
            return ta - tb;
        });

        const total = response.data?.total ?? (response.data?.pagination?.total ?? 0);
        const hasNext = response.data?.has_next ?? (total ? page * limit < total : false);

        chatStore.update(state => ({
            ...state,
            messages: page === 1 ? mapped : [ ...mapped, ...state.messages],
            hasMore: !!hasNext,
            currentPage: page,
            currentChatId: chatId
        }));

        localStorage.setItem('currentChatId', chatId);
    } catch (error) {
        console.error('Error loading chat history:', error);
        chatStore.update(state => ({
            ...state,
            error: 'Failed to load chat history'
        }));
    }
};
 