<script lang="ts">
  import { onMount } from "svelte";
  import ChatView from "./lib/components/ChatView.svelte";
  import { RefreshCw, X } from "lucide-svelte";
  import { InitService } from "./lib/services/init";

  const { settings } = $props();
  const initService = new InitService(settings);

  onMount(async () => {
    await initService.initialize();
  });

  const resetChat = () => {
    initService.resetChat();
  };

  const closeWidget = () => {
    // Dispatch a custom event that the widget can listen to
    const event = new CustomEvent('close-widget');
    document.dispatchEvent(event);
  };
</script>

<main>
  <!-- Header -->
  <header class="main-header" style="background-color: {settings.color_hex};">
    <div class="title">{settings.bot_name}</div>
    <div class="button-group">
      <button class="icon-button" aria-label="Refresh chat" onclick={resetChat}>
        <RefreshCw class="icon" />
      </button>
      <button class="icon-button mobile-only" aria-label="Close chat" onclick={closeWidget}>
        <X class="icon" />
      </button>
    </div>
  </header>

  <div class="content">
    <ChatView {settings} />
  </div>
</main>

