import axios from 'axios';

// Create axios instance for widget settings
export const widgetApi = axios.create({
  baseURL: import.meta.env.VITE_FLUENT_BOT_URL,
  timeout: 30000,
  headers: {
    'Content-Type': 'application/json',
  },
});

// Create axios instance for chat
export const chatApi = axios.create({
  baseURL: import.meta.env.VITE_FLUENT_AI_URL,
  timeout: 30000,
  headers: {
    'Content-Type': 'application/json',
  },
});