import { chatStore, loadChatHistory } from '../stores/chat';
import { v4 as uuidv4 } from 'uuid';
import type { WidgetSettings } from '../types';

export class InitService {
    private settings: WidgetSettings;

    constructor(settings: WidgetSettings) {
        this.settings = settings;
    }

    async initialize() {
        await this.initializeUserId();
        await this.initializeChat();
    }

    private async initializeUserId() {
        const storedId = localStorage.getItem("userId");
        if (!storedId) {
            const newId = uuidv4();
            localStorage.setItem("userId", newId);
        }
    }

    private async initializeChat() {
        const storedChatId = localStorage.getItem("currentChatId");
        if (storedChatId) {
            await loadChatHistory(storedChatId);
        } else if (this.settings.first_message) {
            this.addInitialMessage();
        }
    }

    private addInitialMessage() {
        if (!this.settings.first_message) return;
        
        chatStore.update((store) => ({
            ...store,
            messages: [
                {
                    id: Date.now().toString(),
                    chatId: "",
                    role: "ai",
                    message: this.settings.first_message!,
                    formattedMessage: this.settings.first_message!,
                    created_at: new Date().toISOString(),
                },
            ],
        }));
    }

    resetChat() {
        chatStore.update((store) => ({
            ...store,
            messages: [],
            filters: {
                sources: [],
                dateRange: null,
            }
        }));
        localStorage.removeItem("currentChatId");

        if (this.settings.first_message) {
            this.addInitialMessage();
        }
    }
} 